import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      redirect: '/homePage',
      children: [
        {
          path: 'homePage',
          name: 'homePage',
          title: 'Home',
          component: () => import('@/components/HomePage/homePage'),
        },
        {
          path: 'friend',
          name: 'friend',
          title: 'Friend',
          component: () => import('@/components/Friend/friend'),
        },
        {
          path: 'journal',
          name: 'journal',
          title: 'Journal',
          component: () => import('@/components/Journal/journal'),
        },
        {
          path: 'chart',
          name: 'chart',
          title: 'Chart',
          meta: {
            admin: true,
          },
          component: () => import('@/components/Chart/chart'),
        },
      ],
    },
  ],
});
