<template>
  <div id="home">
    <HeaderPage></HeaderPage>
    <transition name="fade">
      <router-view></router-view>
    </transition>
    <Modal title="Login" v-model="$store.state.loginModal">
      <form style="max-width: 300px;margin: 0 auto;padding: 20px;" action="" @keyup.enter="login">
        <i-input v-model="username" placeholder="username">
          <Icon slot="prepend" type="person"></Icon>
        </i-input>
        <br>
        <i-input type="password" v-model="password" placeholder="password">
          <Icon slot="prepend" type="android-lock"></Icon>
        </i-input>
      </form>
      <div slot="footer">
        <Button type="primary" @click="login">OK</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import sha1 from 'js-sha1';
import { Base64 } from 'js-base64';

export default {
  name: 'home',
  data() {
    return {
      username: '',
      password: '',
    };
  },
  components: {
    HeaderPage: () => import('@/components/Header/header.vue'),
  },
  computed: {},
  methods: {
    login() {
      const userMsg = this.$store.state.userMsg || [];
      userMsg.every((v) => {
        if (v.username === this.username) {
          if (v.password !== sha1(this.password)) {
            this.$Message.error('Wrong password !');
            return false;
          }
          this.$cookie.set(Base64.encode('ManiacUser'), Base64.encode(`Maniac${this.username}`), { expires: '1Y' });
          this.$Message.success('Success !');
          this.$store.commit('setUser', this.username);
          this.$store.commit('setLoginModal', false);
          return false;
        }
        return true;
      });
    },
  },
  mounted() {
  },
  created() {
  },
};
</script>
<style lang="stylus" type="text/stylus">
  #home {
    width: 100%;
    height: 100%
    padding-top 40px
  }
</style>
