import VuePerfectScrollbar from "vue-perfect-scrollbar";

export default {

  install(Vue, options) {
    Vue.component('VuePerfectScrollbar', VuePerfectScrollbar);

    Vue.prototype.isWin = navigator.platform.match(/Win/ig);

    Vue.prototype.iconSize = 16;

    Vue.prototype.locDistance = (dist) => {
      if (dist <= .4) return '';
      else if (dist <= .7) return "15sec";
      else if (dist <= 1) return "25sec";
      else if (dist <= 2) return "1mins";
      else if (dist <= 3) return "2mins";
      else if (dist <= 4) return "2.5mins";
      else if (dist <= 5) return "3mins";
      else if (dist <= 6) return "3.5mins";
      else if (dist <= 7) return "4.5mins";
      else if (dist <= 8) return "5.5mins";
      else if (dist <= 9) return "6mins";
      else if (dist <= 10) return "7mins";
      else if (dist <= 14) return "8mins";
      else if (dist <= 20) return "10mins";
      else if (dist <= 20.5) return "11mins";
      else if (dist <= 22) return "13mins";
      else if (dist <= 25) return "14mins";
      else if (dist <= 26) return "15mins";
      else if (dist <= 28) return "16mins";
      else if (dist <= 30.5) return "17mins";
      else if (dist <= 40) return "18mins";
      else if (dist <= 42) return "19mins";
      else if (dist <= 53) return "21mins";
      else if (dist <= 61.6) return "22mins";
      else if (dist <= 78) return "23mins";
      else if (dist <= 80) return "27mins";
      else if (dist <= 100) return "35mins";
      else if (dist <= 220) return "40mins";
      else if (dist <= 250) return "45mins";
      else if (dist <= 345) return "50mins";
      else if (dist <= 460) return "58mins";
      else if (dist <= 500) return "1hr";
      else if (dist <= 565) return "1hr 7mins";
      else if (dist <= 700) return "1hr 15mins";
      else if (dist <= 720) return "1hr 20mins";
      else if (dist <= 820) return "1hr 25mins";
      else if (dist <= 1000) return "1hr 30mins";
      else return "2hr";
    };

    Vue.prototype.GetDistance = (lat1, lng1, lat2, lng2, number = 10000) => {
      function rad(d) {
        return d * Math.PI / 180.0;
      }

      var radLat1 = rad(lat1);
      var radLat2 = rad(lat2);
      var a = radLat1 - radLat2;
      var b = rad(lng1) - rad(lng2);
      var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) +
        Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(Math.sin(b / 2), 2)));
      s = s * 6378.137;// EARTH_RADIUS;
      s = Math.round(s * number) / number;
      return s;
    };
    Vue.prototype.copyObj = (data) => {
      return JSON.parse(JSON.stringify(data));
    };
  }
};


