import Vue from 'vue';
import App from './App.vue';
import router from './router';
import './plugins/iview';
import store from './store';
// eslint-disable-next-line
import VueClipboard from 'vue-clipboard2';
// eslint-disable-next-line
import { Base64 } from 'js-base64';
import comjs from './assets/commonJS/com';
// eslint-disable-next-line
import Cookie from 'vue-cookie';

Vue.use(VueClipboard);
Vue.use(Cookie);
Vue.use(comjs);

Vue.config.productionTip = false;

router.beforeEach((to, from, next) => {
  if (!to.meta.admin) {
    next();
  } else {
    let user = '';
    const cookie = Base64.decode(Cookie.get('TWFuaWFjVXNlcg'));
    store.state.userMsg.every((v) => {
      if (cookie && v.username === cookie.replace(/Maniac/ig, '')) {
        user = v.username;
        store.commit('setUser', v.username);
        return false;
      }
      return true;
    });

    if (user) {
      next();
    } else {
      store.commit('setLoginModal', true);
      next('home');
    }
  }
});

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
